import { getStyles } from "../shared/style";

const styles = theme => ({
  ...getStyles(theme),
  paper: {
    marginBottom: theme.spacing.unit
  },
  footer: {
    marginTop: theme.spacing.unit
  },
  sync: {
    padding: theme.spacing.unit
  }
});

const OP_DATA = [
  { _id: 1, name: "total distance" },
  { _id: 2, name: "total elevation" },
  { _id: 3, name: "total time" },
  { _id: 8, name: "total kudos" },
  { _id: 4, name: "max distance" },
  { _id: 5, name: "max elevation" },
  { _id: 6, name: "max time" },
  { _id: 9, name: "max kudos" },
  { _id: 7, name: "count" }
];

const GROUP_DATA = [
  { _id: 8, name: "#tatranskakoza (12.11. - 12.12.)" },
  { _id: 1, name: "this week" },
  { _id: 2, name: "last week" },
  { _id: 3, name: "this month" },
  { _id: 4, name: "last month" },
  { _id: 5, name: "this year" },
  { _id: 6, name: "my months" },
  { _id: 7, name: "my years" }
];

const TYPE_DATA = [
  { _id: 0, name: "all" },
  { _id: 1, name: "ride" },
  { _id: 2, name: "run" },
  { _id: 3, name: "walk" },
  { _id: 4, name: "hike" }
];

const buildField = groupId => {
  switch (groupId) {
    case 6:
      return "month";
    case 7:
      return "year";
    default:
      return "userId";
  }
};

const buildOp = opId => {
  switch (opId) {
    case 1:
    case 2:
    case 3:
    case 8:
      return "sum";
    case 4:
    case 5:
    case 6:
    case 9:
      return "max";
    case 7:
    default:
      return "count";
  }
};

const buildOpField = opId => {
  switch (opId) {
    case 1:
    case 4:
      return "dis";
    case 2:
    case 5:
      return "total_elevation_gain";
    case 3:
    case 6:
      return "moving_time";
    case 8:
    case 9:
      return "kudos_count";
    default:
      return "_id";
  }
};

const buildGroup = (groupId, opId) => {
  return {
    field: buildField(groupId),
    op: buildOp(opId),
    opField: buildOpField(opId)
  };
};

const buildTypeFilters = typeId => {
  switch (typeId) {
    case 1:
      return [{ field: "type", value: "Ride" }];
    case 2:
      return [{ field: "type", value: "Run" }];
    case 3:
      return [{ field: "type", value: "Walk" }];
    case 4:
      return [{ field: "type", value: "Hike" }];
    case 0:
    default:
      return [];
  }
};

const buildGroupFilters = (groupId, user) => {
  switch (groupId) {
    case 1:
      return [
        {
          field: "start_date",
          op: "lte",
          value: moment()
            .endOf("week")
            .toDate()
        },
        {
          field: "start_date",
          op: "gte",
          value: moment()
            .startOf("week")
            .toDate()
        }
      ];
    case 2:
      return [
        {
          field: "start_date",
          op: "lte",
          value: moment()
            .subtract(1, "weeks")
            .endOf("week")
            .toDate()
        },
        {
          field: "start_date",
          op: "gte",
          value: moment()
            .subtract(1, "weeks")
            .startOf("week")
            .toDate()
        }
      ];
    case 3:
      return [
        {
          field: "start_date",
          op: "lte",
          value: moment()
            .endOf("month")
            .toDate()
        },
        {
          field: "start_date",
          op: "gte",
          value: moment()
            .startOf("month")
            .toDate()
        }
      ];
    case 4:
      return [
        {
          field: "start_date",
          op: "lte",
          value: moment()
            .subtract(1, "month")
            .endOf("month")
            .toDate()
        },
        {
          field: "start_date",
          op: "gte",
          value: moment()
            .subtract(1, "month")
            .startOf("month")
            .toDate()
        }
      ];
    case 5:
      return [
        {
          field: "start_date",
          op: "lte",
          value: moment()
            .endOf("year")
            .toDate()
        },
        {
          field: "start_date",
          op: "gte",
          value: moment()
            .startOf("year")
            .toDate()
        }
      ];
    case 6:
      return [{ field: "userId", value: user._id }];
    case 7:
      return [{ field: "userId", value: user._id }];
    case 8:
      return [
        // { field: 'name', op: 'regex', value: 'tatranskakoza' },
        {
          field: "start_date",
          op: "lte",
          value: new Date(2020, 11, 12, 23, 59, 0, 0)
        },
        {
          field: "start_date",
          op: "gte",
          value: new Date(2020, 10, 12, 0, 0, 0, 0)
        }
      ];
    default:
      return [];
  }
};

const buildCell = x => {
  const value = _.get(x, "_user.name");
  return (
    <Link
      color="inherit"
      href={`https://www.strava.com/athletes/${x._user.strava.oAuthId}`}
      target="_blank"
      rel="noopener noreferrer"
    >
      {value}
    </Link>
  );
};

const buildIdRow = groupId => {
  switch (groupId) {
    case 6:
      return {
        field: "_id",
        label: "Months",
        filterable: false,
        disablePadding: true
      };
    case 7:
      return {
        field: "_id",
        label: "Years",
        filterable: false,
        disablePadding: true
      };
    default:
      return {
        field: "_user.name",
        label: "User",
        filterable: false,
        sortable: false,
        disablePadding: true,
        cell: buildCell
      };
  }
};

const buildValRow = opId => {
  switch (opId) {
    case 1:
    case 4:
      return { field: "val", label: "Distance *", filterable: false };
    case 2:
    case 5:
      return { field: "val", label: "Elevation", filterable: false };
    case 3:
    case 6:
      return { field: "val", label: "Time", filterable: false };
    case 7:
      return { field: "val", label: "Count", filterable: false };
    case 8:
    case 9:
      return { field: "val", label: "Kudos", filterable: false };
    default:
      return { field: "val", label: "Value", filterable: false };
  }
};

const buildRows = (groupId, opId) => {
  return [buildIdRow(groupId), buildValRow(opId)];
};

const buildFilters = getUsersInClub => {
  return [
    // {
    //   label: 'Type', field: 'type', data: [
    //     { _id: 1, name: "Ride", filters: [{ field: 'type', op: 'eq', value: 'Ride' }] },
    //     { _id: 2, name: "Run", filters: [{ field: 'type', op: 'eq', value: 'Run' }] },
    //     { _id: 3, name: "Walk", filters: [{ field: 'type', op: 'eq', value: 'Walk' }] },
    //     { _id: 4, name: "Hike", filters: [{ field: 'type', op: 'eq', value: 'Hike' }] },
    //   ]
    // },
    {
      label: "Club",
      field: "club",
      data: [
        {
          _id: 1,
          name: "VYPADNI VON",
          filters: [
            { field: "userId", op: "in", value: () => getUsersInClub(562456) }
          ]
        },
        {
          _id: 2,
          name: "TATRANSK KOZY",
          filters: [
            { field: "userId", op: "in", value: () => getUsersInClub(771781) }
          ]
        }
      ]
    },
    {
      label: "Sex",
      field: "sex",
      data: [
        {
          _id: 1,
          name: "Male",
          filters: [{ field: "sex", op: "eq", value: "M" }]
        },
        {
          _id: 2,
          name: "Female",
          filters: [{ field: "sex", op: "eq", value: "F" }]
        }
      ]
    }
  ];
};

let _groupFilters, _sort, _page, _group, _filters, _typeFilters, _timer, _searchFilters;
class TableWrapper extends React.Component {
  constructor(props) {
    super(props);

    const groupId = 8;
    this.state = {
      opId: 1,
      groupId,
      typeId: 0,
      search: ''
    };
    _groupFilters = buildGroupFilters(groupId, props.user.user);
    _group = buildGroup(groupId, 1);
    _typeFilters = [];
    _searchFilters = [];
  }

  handleChange = (filters, page, sort) => {
    _filters = filters;
    _sort = sort;
    _page = page;
    this.getList();
  };

  getList = async () => {
    try {
      const f = [..._groupFilters, ..._filters, ..._typeFilters, ..._searchFilters];
      return await this.props.store.getActivities(_group, f, _page, _sort);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  isDisabled = x => {
    return true;
  };

  handleChangeType = (name, value) => {
    this.setState({ [name]: value });
    _typeFilters = buildTypeFilters(value);
    this.getList();
  };

  handleChangeOp = (name, value) => {
    this.setState({ [name]: value });
    _group = buildGroup(this.state.groupId, value);
    this.getList();
  };

  handleChangeGroup = (name, value) => {
    const { user } = this.props.user;

    this.setState({ [name]: value });
    _groupFilters = buildGroupFilters(value, user);
    _group = buildGroup(value, this.state.opId);
    this.getList();
  };

  handleChangeSearch = (name, value) => {
    this.setState({ [name]: value });

    clearTimeout(_timer); // doesn't matter if it's 0
    _timer = setTimeout(() => {
      _searchFilters = value ? [{ field: 'name', op: 'regex', value }] : []
      this.getList();
    }, 500);
  }

  render() {
    const { opId, groupId, typeId } = this.state;
    const { classes } = this.props;

    return (
      <div>
        <Paper className={classnames(classes.paper, classes.row)}>
          <Select
            label="Type"
            value={typeId}
            field="typeId"
            onChange={this.handleChangeType}
            data={TYPE_DATA}
            textField="name"
            required
          />
          <Select
            label="Stats"
            value={opId}
            field="opId"
            onChange={this.handleChangeOp}
            data={OP_DATA}
            textField="name"
            required
          />
          <Select
            label="Group"
            value={groupId}
            field="groupId"
            onChange={this.handleChangeGroup}
            data={GROUP_DATA}
            textField="name"
            required
          />
          <div className={classes.grow}>
            {/* <TextInput
              value={this.state.search}
              field="search"
              onChange={this.handleChangeSearch}
              fullWidth
              startAdornment={
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              }
            /> */}
          </div>
          {this.props.user.user.strava && (
            <span className={classes.sync}>
              <StravaButton title="sync" />
            </span>
          )}
        </Paper>
        <Table
          rows={buildRows(this.state.groupId, this.state.opId)}
          data="activities"
          onChange={this.handleChange}
          // onDelete={deleteTimeRecord}
          title="Activities"
          // onEdit={this.onEdit}
          store={this.props.store}
          filters={buildFilters(this.props.store.getUsersInClub)}
          // defaultFilters={this.props.defaultFilters}
          defaultSort={{ field: "val", dir: "desc" }}
          isDisabled={this.isDisabled}
        />
        <Typography variant="body1" className={this.props.classes.footer}>
          * bike distance calc as distance * 0.33
        </Typography>
      </div>
    );
  }
}

TableWrapper.propTypes = {};

export default compose(withStore, withUser, withStyles(styles))(TableWrapper);
